html,
body,
#root {
  height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #dbe2ef;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar-container {
  height: 10%;
  width: 80%;
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  padding-top: 16px;
  padding-bottom: 16px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  box-sizing: border-box;
  margin: 0 auto;
}

a {
  text-decoration: none;
  font-size: 18px;
  font-weight: 400;
  color: #112d4e;
  padding-left: 10px;
}

a:hover {
  text-decoration: underline;
}

.selected {
  font-weight: bold;
}

@media only screen and (max-width: 768px) {
  .navbar-container {
    width: 100%;
  }
}

@media only screen and (max-width: 400px) {
}

.homepage-container {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  height: 90%;
  min-height: 750px;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.personal-info {
  height: auto;
  max-width: 500px;
  padding-top: 30px;
  text-align: center;
  position: relative;
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

.personal-info p {
  font-size: 21px;
  line-height: 28px;
  color: #113f67;
}

.personal-info h1 {
  font-size: 24px;
  color: #113f67;
}

.avatar {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
}

.avatar img {
  -webkit-align-self: flex-end;
          align-self: flex-end;
  height: 350px;
}

.social-icons {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  box-sizing: border-box;
  min-height: 160px;
  max-height: 180px;
}

.icon-container {
  margin: 20px 20px 0 20px;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.icon-container {
}

.icon-container:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
/* .below {
  align-self: flex-end;
  margin-bottom: 0px;
}

.above {
  align-self: flex-start;
  margin-top: 0px;
} */

.github,
.linkedin {
  -webkit-align-self: flex-end;
          align-self: flex-end;
}

.instagram,
.twitter {
  -webkit-align-self: flex-start;
          align-self: flex-start;
}

.github-icon,
.twitter-icon,
.instagram-icon,
.linkedin-icon {
  color: #009f9d;
  opacity: 1;
}

.github-icon:hover,
.twitter-icon:hover,
.instagram-icon:hover,
.linkedin-icon:hover {
  color: #0f0a3c;
}

.github-icon:hover {
  fill: #34699a;
}

.twitter-icon:hover {
  fill: #8ed6ff;
}

.instagram-icon:hover {
  fill: #e14594;
}
.linkedin-icon:hover {
  fill: #408ab4;
}
.personal-info p {
  text-align: center;
}

@media only screen and (max-width: 468px) {
  .personal-info p {
    font-size: 18px;
  }
}

@media only screen and (max-width: 468px) {
  .personal-info,
  .social-icons {
    width: 90vw;
  }

  .github-icon,
  .twitter-icon,
  .instagram-icon,
  .linkedin-icon {
    height: 40px;
    width: 40px;
  }

  .icon-container {
    margin: 10px 10px 0 10px;
  }

  .instagram,
  .twitter {
    -webkit-align-self: center;
            align-self: center;
  }

  .avatar img {
    height: 250px;
  }
}

.aboutme-page-container {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  text-align: left;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.aboutme {
  /* display: flex; */
  margin-top: 20px;
  font-size: 18px;
  line-height: 28px;
  color: #113f67;
}

.footer {
  height: 20px;
}

.bold {
  font-weight: bold;
}

.aboutme a {
  text-decoration: underline;
}

