.design-page-container {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.designs {
  margin-top: 20px;
  font-size: 18px;
  line-height: 28px;
  color: #113f67;
}

.designs a {
  padding: 0;
  color: #e14594;
  font-weight: bold;
}

.App {
  display: -webkit-flex;
  display: -ms-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
  max-width: 768px;
  margin: 0 auto;
  padding: 0 20px;
  box-sizing: border-box;
  font-family: "Work Sans", "Roboto", sans-serif;
}

